.card {
  background: var(--dark-card);
  color: var(--dark-text-sec);
  padding: 1.625rem;
  border-radius: 0.1875rem;
  font-size: 0.75rem;
  font-weight: 700;
  text-align: center;
}
.card.large {
  border-top: 4px solid;
  margin-bottom: 1.5rem;
  /* Gradient borders aren't supported by using CSS (https://www.geeksforgeeks.org/gradient-borders) */
}
@media (min-width: 71.875em) {
  .card.large {
    max-width: 15.9375rem;
    max-height: 13.4375rem;
  }
}
.card.large.facebook {
  border-color: var(--facebook);
}
.card.large.twitter {
  border-color: var(--twitter);
}
.card.large.instagram {
  border-color: var(--transparent);
  border-image: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
  border-image-slice: 1;
}
.card.large.youtube {
  border-color: var(--youtube);
}
.card.small {
  margin-bottom: 1rem;
}
.card p {
  margin: 0;
  padding: 0;
  line-height: 1.1;
}
.card .platform {
  display: flex;
  align-items: center;
  justify-content: center;
}
.card .content .count {
  color: var(--dark-text-primary);
  font-size: 3.5rem;
  letter-spacing: -0.125rem;
}
.card .content .label {
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.5em;
}
.card .change p {
  display: inline-block;
}
.card .change.increase {
  color: var(--change-up);
}
.card .change.increase::before {
  content: "▴";
}
.card .change.decrease {
  color: var(--change-down);
}
.card .change.decrease::before {
  content: "▾";
}